import axios from "axios"
import { useCallback, useEffect, useMemo, useState } from "react"
import { getContract, getProvider } from "../../client/helper"
import { deCode } from "../../utils/utils"
import abi from "../../abi/DLensImplementation.json"
import { BigNumber } from "ethers"
import { ENGINE_LENS } from "../../utils/constant"
import "./manual.scss"

export default function Manual() {
  const chainList = useMemo(() => {
    return [
      {
        chainId: 42161,
        url: "https://api.arbiscan.io/api",
        gatewayAddress: "0x7c4a640461427c310a710d367c2ba8c535a7ef81"
      },
      {
        chainId: 324,
        url: "https://block-explorer-api.mainnet.zksync.io/api",
        gatewayAddress: "0x34fd72d2053339ea4eb1a8836cf50ebce91962d0"
      },
      {
        chainId: 59144,
        url: "https://api.lineascan.build/api",
        gatewayAddress: "0xe840Bb03fE58540841e6eBee94264d5317B88866"
      },
      {
        chainId: 534352,
        url: "https://api.scrollscan.com/api",
        gatewayAddress: "0x7B56Af65Da221A40B48bEDcCb67410D6C0bE771D"
      },
    ]
  }, [])
  const [address, setAddress] = useState("")
  const [chainId, setChainId] = useState<number>()
  const [tokenId, setTokenId] = useState("")
  const [typeInfo, setTypeInfo] = useState("")
  const [tokenInfo, setTokenInfo] = useState<any>()
  const [isNoFound, setIsNoFound] = useState(false)
  const getLTokenId = useCallback(async () => {
    let chain = chainList.find((item: any) => item.chainId === chainId);
    if (!chain) {
      throw new Error(`Invalid network ${chainId}`);
    }
    let url: string = chain?.url
    let addLiquidity = "requestAddLiquidity";
    let removeLiquidity = "requestRemoveLiquidity";
    let res = await axios.get(url, {
      params: {
        module: "account",
        action: "txlist",
        address: address,
        endblock: "latest",
        page: 1,
        offset: 100,
        sort: "desc",
        apikey: "USDZ3V12NJ3RF4NCBQQYQ1N81KYS6IWYJA"
      }
    })
    if (res.data && res.data.result.length > 0 && +res.data.status) {
      let data = res.data.result;
      let obj = data.find((item: any) => item.functionName.includes(addLiquidity) || item.functionName.includes(removeLiquidity))
      if (obj) {
        const provider: any = getProvider(chain.chainId)
        const res = await provider.getTransactionReceipt(obj.hash)
        let logs = res?.logs
        let methodData = logs[logs.length - 1]?.data
        let idInfo = deCode(methodData, "liquidity")
        let id = String(idInfo?.lTokenId);
        setTokenId(id)
      }
    }

  }, [address, chainId, chainList])

  const getPTokenId = useCallback(async () => {
    let chain = chainList.find((item: any) => item.chainId === chainId);
    if (!chain) {
      throw new Error(`Invalid network ${chainId}`);
    }
    let url: string = chain?.url
    let trade = "requestTrade";
    let res = await axios.get(url, {
      params: {
        module: "account",
        action: "txlist",
        address: address,
        endblock: "latest",
        page: 1,
        offset: 100,
        sort: "desc",
        apikey: "USDZ3V12NJ3RF4NCBQQYQ1N81KYS6IWYJA"
      }
    })
    if (res.data && res.data.result.length > 0 && +res.data.status) {
      let data = res.data.result;
      let obj = data.find((item: any) => item.functionName.includes(trade))
      if (obj) {
        const provider: any = getProvider(chain.chainId)
        const res = await provider.getTransactionReceipt(obj.hash)
        let logs = res?.logs
        let methodData = logs[logs.length - 1]?.data
        let idInfo = deCode(methodData, "trade")
        let id = String(idInfo?.pTokenId);
        setTokenId(id)
      }
    }
  }, [address, chainId, chainList])

  const getLpState = useCallback(async () => {
    try {
      const provider: any = getProvider(20231119)
      const contract = getContract(ENGINE_LENS, abi, provider)
      const res = await contract.getLpState(tokenId)
      console.log("getLpState", res)
      let obj = {
        requestId: "",
        liquidity: "",
        cumulativePnl: "",
        cumulativePnlPerLiquidity: "",
        lTokenId: ""
      }
      obj.requestId = BigNumber.from(res?.requestId._hex).toString();
      obj.lTokenId = BigNumber.from(res?.lTokenId._hex).toString();
      obj.cumulativePnl = BigNumber.from(res?.cumulativePnl._hex).toString();
      obj.cumulativePnlPerLiquidity = BigNumber.from(res.cumulativePnlPerLiquidity._hex).toString();
      obj.liquidity = BigNumber.from(res.liquidity._hex).toString();
      setTokenInfo(obj);
    } catch (error) {
      setIsNoFound(true)
    }
  }, [tokenId])

  const getTdState = useCallback(async () => {
    try {
      const provider: any = getProvider(20231119)
      const contract = getContract(ENGINE_LENS, abi, provider)
      const res = await contract.getTdState(tokenId)
      console.log("getTdState", res)
      let obj = {
        requestId: "",
        liquidated: "false",
        pTokenId: "",
        cumulativePnl: "",
        positions: []
      }
      let positions = res?.positions.map((item: any) => {
        let obj = {
          cost: "",
          cumulativeFundingPerVolume: "",
          volume: "",
          symbol: "",
          symbolId: "",
        }
        obj.cost = BigNumber.from(item.cost._hex).toString()
        obj.cumulativeFundingPerVolume = BigNumber.from(item.cumulativeFundingPerVolume._hex).toString()
        obj.symbol = item.symbol;
        obj.symbolId = item.symbolId;
        obj.volume = BigNumber.from(item.volume._hex).toString()
        return obj
      })
      console.log(positions)
      obj.liquidated = res.liquidated ? "true" : "false";
      obj.requestId = BigNumber.from(res?.requestId._hex).toString();
      obj.pTokenId = BigNumber.from(res?.pTokenId._hex).toString();
      obj.cumulativePnl = BigNumber.from(res?.cumulativePnl._hex).toString();
      obj.positions = positions
      setTokenInfo(obj);
    } catch (error) {
      setIsNoFound(true)
    }

  }, [tokenId])

  const getLoadTokenInfo = useCallback(() => {
    if (tokenId) {
      if (typeInfo === "lp") {
        getLpState()
      } else {
        getTdState()
      }
    } else {
      alert("Unable to complete the query. Please check that you've filled all the required information")
    }
  }, [getLpState, getTdState, tokenId, typeInfo])

  const selectTokenId = useCallback(() => {
    if (address && chainId && typeInfo) {
      if (typeInfo === "lp") {
        getLTokenId()
      } else {
        getPTokenId()
      }
    } else {
      alert("Unable to complete the query. Please check that you've filled all the required information")
    }

  }, [address, chainId, getLTokenId, getPTokenId, typeInfo])

  const onChageTokenId = useCallback((e: any) => {
    let value = e.target.value
    setTokenId(value)
  }, [])

  const onChageAddrress = useCallback((e: any) => {
    let value = e.target.value
    setAddress(value)
  }, [])

  return <div className="manual-box">
    <div className="title">
      Check Positions and Liquidity Status on Deri
    </div>
    <div className="step">
      <div className="step-title">Step 1: Paste the wallet address you wish to query.</div>
      <div>
        <input type="text" onChange={onChageAddrress} value={address} />
      </div>
    </div>
    <div className="step">
      <div className="step-title">Step 2: Choose the iChain on which you've traded or provided liquidity.</div>
      <div>
        <input type="radio" name="chain" id="arbitrum" value={42161} onChange={() => { setChainId(42161); setTokenId("") }} /> <label htmlFor="arbitrum">Arbitrum</label>
        <input type="radio" name="chain" id="zkSync" value={324} onChange={() => { setChainId(324); setTokenId("") }} /> <label htmlFor="zkSync">zkSync Era</label>
        <input type="radio" name="chain" id="linea" value={59144} onChange={() => { setChainId(59144); setTokenId("") }} /> <label htmlFor="linea">Linea</label>
        <input type="radio" name="chain" id="scroll" value={534352} onChange={() => { setChainId(534352); setTokenId("") }} /> <label htmlFor="scroll">Scroll</label>
      </div>
    </div>
    <div className="step">
      <div className="step-title">Step 3: Select your role - either Trader or LP (Liquidity Provider) .</div>
      <div>
        <input type="radio" name="typeTokenId" id="trade" value="trade" onChange={() => { setTypeInfo("trade"); setTokenId("") }} /> <label htmlFor="trade">Trader</label>
        <input type="radio" name="typeTokenId" id="lp" value="lp" onChange={() => { setTypeInfo("lp"); setTokenId("") }} /><label htmlFor="lp">LP (Liquidity Provider)</label>
      </div>
    </div>
    <div className="step">
      <div className="step-title">Step 4: <span onClick={selectTokenId}>Click</span> to automatically retrieve the latest dToken ID, or manually enter the dToken ID for the next step.</div>
      <div>DToken ID: <input className="tokenid-input" onChange={onChageTokenId} type="text" value={tokenId} /> {isNoFound ? " (not found)" : null} </div>
    </div>
    <div className="step">
      <div className="step-title">Step 5:
        <span onClick={getLoadTokenInfo}>Click</span> to Display the current state of your dToken.
      </div>
      <table>
        {tokenInfo && Object.keys(tokenInfo).map((key: any, index: number) => {
          return key === "positions" ? null : <tr key={index}>
            <td>{key}</td>
            <td>{tokenInfo[key]}</td>
          </tr>
        })}
      </table>
      {tokenInfo?.positions ? <>
        <div className="position-box">Positions</div>
        <table>
          {tokenInfo?.positions.map((position: any) => {
            return Object.keys(position).map((posKey: any, index: number) => {
              return <tr key={index}>
                <td>{posKey}</td>
                <td>{position[posKey]}</td>
              </tr>
            })
          })}
        </table>
      </> : null}
    </div>
  </div>
}