import { AddressZero } from "@ethersproject/constants";
import { getAddress } from "@ethersproject/address";
import { JsonRpcProvider, JsonRpcSigner } from "@ethersproject/providers";
import { Contract } from "@ethersproject/contracts";
import { RPC_PROVIDERS, SUPPORTED_NETWORKS } from "../utils/constant";
import { ethers } from "ethers";
import { random } from "../utils/utils";
// account is not optional
export function getSigner(provider: JsonRpcProvider, account: string): JsonRpcSigner {
  return provider.getSigner(account).connectUnchecked();
}

// account is optional
export function getProviderOrSigner(
  provider: JsonRpcProvider,
  account?: string
): JsonRpcProvider | JsonRpcSigner {
  return account ? getSigner(provider, account) : provider;
}

// account is optional
export function getContract(
  address: string,
  ABI: any,
  provider: JsonRpcProvider,
  account?: string
): Contract {
  if (!isAddress(address) || address === AddressZero) {
    throw Error(`Invalid 'address' parameter '${address}'.`);
  }

  return new Contract(address, ABI, getProviderOrSigner(provider, account) as any);
}

export function isAddress(value: any): string | false {
  try {
    return getAddress(value);
  } catch {
    return false;
  }
}

export function getProviderRpc(chainId: number) {
  const providers = RPC_PROVIDERS[chainId];
  return providers[random(0, providers.length - 1)];
}

export function getProvider(chainId: number, isTestnet?: boolean) {
  const rpc = getProviderRpc(chainId);
  const network = SUPPORTED_NETWORKS.find((network) => network.chainId === chainId);
  if (!network) {
    throw new Error(`Invalid network ${chainId}`);
  }
  return new ethers.providers.StaticJsonRpcProvider(rpc, {
    chainId,
    name: network.name,
  });
}